import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { NavigationExtras, Router } from '@angular/router';
import { ToastController } from '@ionic/angular';
import { ApiService } from 'src/app/services/api.service';

@Component({
  selector: 'app-new-passwordchange',
  templateUrl: './new-passwordchange.page.html',
  styleUrls: ['./new-passwordchange.page.scss'],
})
export class NewPasswordchangePage implements OnInit {
  newPassword = new FormControl('');
  confirmPassword = new FormControl('');
  phoneNumber: string | number = localStorage.getItem('customerPhonenum');

  async openToast(errorMessage: string) {
    const toast = await this.toastCtrl.create({
      message: `${errorMessage}`,
      duration: 2500,
      position: 'bottom',
    });
    toast.present();
  }
  constructor(
    private router: Router,
    private toastCtrl: ToastController,
    private api: ApiService
  ) { }

  ngOnInit() {
    this.newPassword.patchValue('');
    this.confirmPassword.patchValue('');
  }

  back() {
    this.router.navigateByUrl('/login');
  }

  updatePassword() {
    if (this.newPassword.value === '' || this.confirmPassword.value === '') {
      this.openToast('Fill all fields');
      return;
    }
    if (this.newPassword.value !== this.confirmPassword.value) {
      this.openToast("Passwords didn't match");
      return;
    }
    const data = {
      phoneNumber: this.phoneNumber,
      custPassword: this.newPassword.value,
    };
    this.api.updateCustomerPassword(data).subscribe((resp: any) => {
      if (resp?.status == 200) {
        const navigationExtras: NavigationExtras = {
          queryParams: {
            'screenDetails': 'Password Changed!',
            'screenDescription': 'Your password has been changed successfully',
            'screenName': 'new-passwordchange'
          },
        };
        this.api.sendNavParam(navigationExtras);
        this.router.navigateByUrl('/success-message');
        this.openToast('Password updated successfully');
        this.newPassword.patchValue('');
        this.confirmPassword.patchValue('');
      } else {
        this.router.navigate(['/login'], { replaceUrl: true });
        this.openToast(resp?.message);
      }
    });
  }
}
